export const FLAG_ICON_TYPE = {
  UA: 'ua',
  ENG: 'eng',
}

const flagIconByType = (type) => {
  if (type === FLAG_ICON_TYPE.ENG) {
    return "";
  }

  if (type === FLAG_ICON_TYPE.UA) {
    return "";
  }

  return "";
}

const FlagIcon = ({ type }) => {
  return <span className="inline-flex items-center justify-center text-2xl w-[24px] h-[24px]">{flagIconByType(type)}</span>;
}

export default FlagIcon;
